@import url("https://fonts.googleapis.com/css?family=Roboto:400,900&display=swap");
* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  line-height: 1.5;
  height: 100%;
  width: 100%;
}

body {
  color: #333;
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-size: 16px;
  font-size: 1.6rem;
  overflow-x: hidden;
  margin: 0;
  background: #fff;
  height: 100%;
  width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.35em;
  font-weight: normal;
  font-family: "Roboto", sans-serif;
  outline: 0;
  text-transform: uppercase;
}

h1 {
  font-size: 28px;
  font-size: 2.8rem;
  line-height: 1.1;
}
@media all and (min-width: 76.7rem) {
  h1 {
    font-size: 48px;
    font-size: 4.8rem;
  }
}

h2 {
  font-size: 24px;
  font-size: 2.4rem;
  line-height: 1.2;
}
@media all and (min-width: 76.7rem) {
  h2 {
    font-size: 24px;
    font-size: 2.4rem;
  }
}

h3 {
  font-size: 18px;
  font-size: 1.8rem;
  line-height: 1.2;
}
@media all and (min-width: 76.7rem) {
  h3 {
    font-size: 18px;
    font-size: 1.8rem;
  }
}

h4 {
  font-size: 10.6px;
  font-size: 1.06rem;
  line-height: 1.2;
}
@media all and (min-width: 76.7rem) {
  h4 {
    font-size: 10.6px;
    font-size: 1.06rem;
  }
}

h5 {
  font-size: 10px;
  font-size: 1rem;
  line-height: 1.2;
}
@media all and (min-width: 76.7rem) {
  h5 {
    font-size: 10px;
    font-size: 1rem;
  }
}

h6 {
  font-size: 8.7px;
  font-size: 0.87rem;
  line-height: 1.2;
}
@media all and (min-width: 76.7rem) {
  h6 {
    font-size: 8.7px;
    font-size: 0.87rem;
  }
}

img {
  width: auto;
  max-width: 100%;
  height: auto !important;
}

a {
  text-decoration: none;
  color: #333;
  transition: color 0.25s ease;
}
a:hover {
  outline: 0;
}
ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0 0 1.5em;
}

.main-layout {
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  justify-content: flex-end;
}
@media all and (min-width: 76.7rem) {
  .main-layout {
    flex-direction: row;
    align-items: stretch;
    justify-content: center;
  }
}
.main-layout__first {
  outline: 1px solid;
  background: #333;
  color: #fff;
  flex-shrink: 1;
  height: 100%;
  width: 100%;
  padding: 2rem;
  font-size: 0.9em;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
@media all and (min-width: 76.7rem) {
  .main-layout__first {
    height: 100%;
    position: static;
    width: 32rem;
    overflow-y: auto;
  }
}
.main-layout__second {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  postion: relative;
  z-index: 2;
  background: #eff0f1;
  padding: 2rem;
}
@media all and (min-width: 76.7rem) {
  .main-layout__second {
    justify-content: center;
    flex-grow: 1;
  }
}

.cards {
  display: flex;
  white-space: nowrap;
  flex-wrap: wrap;
  justify-content: center;
}
.cards .card {
  margin: 0.7rem;
}
@media all and (min-width: 76.7rem) {
  .cards .card {
    margin: 1rem;
  }
}

#votes + .cards {
  margin-top: 2rem;
}
@media all and (min-width: 76.7rem) {
  #votes + .cards {
    margin-top: 4rem;
  }
}

