.card {
  display: block;
  position: relative;
}
.card .card__indicator {
  appearance: none;
  padding: 0.7em 1.1em;
  box-shadow: 0 1px 1px #717171;
  border-radius: 0.2em;
  background-color: #333;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  line-height: 1.2;
  transition: all 0.25s ease;
  display: inline-block;
  outline: 0;
  border: 0;
  cursor: pointer;
}
.card .card__indicator:active, .card .card__indicator:hover, .card .card__indicator:focus {
  background-color: #09D;
  text-decoration: none;
  color: #fff;
}
.card .card__indicator:disabled, .card .disabled.card__indicator {
  background: #D2D2D2;
  border-color: #D2D2D2;
  cursor: default;
}
@media all and (min-width: 76.7rem) {
  .card .card__indicator {
    font-size: 1.56em;
    padding: 0.5em 1em !important;
  }
}
.card input[type=radio]:checked + .card__indicator {
  background-color: #09D;
  text-decoration: none;
  color: #fff;
}
.card input[type=radio] {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
}
.card .votes {
  border-radius: 50%;
  display: block;
  position: absolute;
  background: #fff;
  min-width: 2rem;
  line-height: 2rem;
  text-align: center;
  font-size: 0.8em;
  box-shadow: 1px 1px 5px #9f9f9f;
  top: -1rem;
  right: -0.5em;
}
@media all and (min-width: 76.7rem) {
  .card .votes {
    min-width: 3rem;
    line-height: 3rem;
    top: -1.6rem;
    right: -0.8em;
    font-size: inherit;
  }
}

#votes {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: flex-end;
}
#votes > li {
  appearance: none;
  padding: 0.7em 1.1em;
  box-shadow: 0 1px 1px #717171;
  border-radius: 0.2em;
  background-color: #09D;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  line-height: 1.2;
  transition: all 0.25s ease;
  display: inline-block;
  outline: 0;
  border: 0;
  cursor: pointer;
  margin: 0.7rem;
}
#votes > li:active, #votes > li:hover, #votes > li:focus {
  background-color: #09D;
  text-decoration: none;
  color: #fff;
}
#votes > li:disabled, #votes > li.disabled {
  background: #D2D2D2;
  border-color: #D2D2D2;
  cursor: default;
}
@media all and (min-width: 0) and (max-width: 76.7rem) {
  #votes > li {
    font-size: 0.75em;
    padding: 0.5em 1em !important;
  }
}
#votes > li:active, #votes > li:hover, #votes > li:focus {
  background-color: #09D;
  text-decoration: none;
  color: #fff;
  cursor: default;
}
@media all and (min-width: 76.7rem) {
  #votes > li {
    appearance: none;
    padding: 0.7em 1.1em;
