  }
}
#votes .vote {
  width: 1.5em;
  height: 1.5em;
  display: inline-block;
  background: white;
  color: #333;
  line-height: 1.5;
  margin-left: 0.8em;
  box-shadow: inset 0px 0px 2px #333;
}

#topic-wrapper {
  max-width: 80rem;
  width: 100%;
  margin-top: 2em;
  display: flex;
  align-items: flex-end;
}
@media all and (min-width: 76.7rem) {
  #topic-wrapper {
    margin-top: 3em;
  }
}
#topic-wrapper textarea {
  appearance: none;
  border: 0;
  background: none;
  outline: 0;
  width: 100%;
  padding: 0;
  border-bottom: 2px solid;
  font-size: 18px;
  font-size: 1.8rem;
  resize: none;
}
@media all and (min-width: 76.7rem) {
  #topic-wrapper textarea {
    font-size: 28px;
    font-size: 2.8rem;
  }
}
#topic-wrapper button.history {
  appearance: none;
  padding: 0.7em 1.1em;
  box-shadow: 0 1px 1px #717171;
  border-radius: 0.2em;
  background-color: #2f2f2f;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  line-height: 1.2;
