#votes > li.avoid-vertical-jump,
.avoid-vertical-jump {
  visibility: hidden !important;
  width: 0 !important;
  overflow: hidden !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.main-layout__first.history-list-wp {
  display: none;

  &.active {
    display: block;
  }
}

.main-layout__first.active + .main-layout__first {
  display: none;
}
